/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
$dark: #272635; 
$breakpoint-mob: 480px;
$breakpoint-tab: 768px;
$breakpoint-desk: 1000px;

.flex-container {
    display: flex;
}
.flex-row {
    flex-direction: row;
}
.flex-col {
    flex-direction: column;
}

.grid-container {
    display: grid;
}
//Mobile
.nav-outer {
    width: 100%;
}
.nav-social {
    > a {
        font-size: 1.25em;
        margin-left: 1em;
        text-decoration: none;
        color: $dark;
        :hover {
            transform: scale(1.1); //fix
        }
    }    
}
.nav-container {
    margin: 0.5em 0.5em 0.5em 0;
}
.nav-title > a,
.nav-container {
    text-decoration: none;
    color: $dark;
}
.navbar ul {
    > li {
        list-style-type: none;
        margin-left: 1em;
        > a {
            text-decoration: none;
            color: $dark;
            font-size: 1.1em;
        }
    }
}
#nav-button {
    display: inline-block;
    z-index: 25;
    margin-right: 2em;
    text-decoration:none;
    font-weight:600;
}
.off-canvas {
    display: none;
    opacity: 0;
    transition: opacity 1s;
}       
.off-canvas.active {
    display: block;
    opacity: 1;
    width: 100%;
    height: 10vh;
    position: absolute;
    margin-top: 0;
    z-index: 2;
    color: $dark;
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 0 0 5px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    .navbar {
        background-color: rgba(255, 255, 255, 1);
    }
    > * {
        color: $dark;
    }
    .navbar > ul > li {
        margin-top: 1em;
    }
    .nav-right {
        > * {
            display: block;
            margin-left: 2em;
            text-decoration: none;
        }
        > .nav-social {
            margin-top: 1em;
        }
        background-color: rgba(255, 255, 255, 1);
        box-shadow: 0 0 5px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
        padding-top: 1em;
        margin-bottom: 1em;
        padding-bottom: 1em;
    }
}
.nav-container.active > .navbar {
    height: 100%;
}

// Tablet
@media (min-width: $breakpoint-tab) {
    #nav-button {
        display: none;
    }
    .off-canvas {
        display: flex;
        opacity: 1;
        width: 100%;
        position: relative;
        height: auto;
    }
    .nav-title {
        display: none;
    }   
    .navbar {
        height: auto;
        flex: 0 1 auto;
        padding: 0.25em;
        // justify-content: flex-start;
        > ul {
            display: flex;
            justify-content: space-around;
            flex-direction: row;
        }
    }

    .nav-container {
        justify-content: space-between;
        > .navbar {
            height: auto;
        }
    } 
    
    .nav-right {
        flex: 1;
        width: 100%;
        margin-left:10%;
        justify-content: space-around;
        padding: 0;
        margin: 0;
        > * {
            display: flex;
            margin-left: auto;
        }
        > .btn {
            display: inline-flex;
            text-decoration: none;
        }
    }
    .nav-social {
        margin-left: 20%;
        margin-top: 0.75em;
    }
}


/**
 * Desktop
 */
@media (min-width: $breakpoint-desk) {

}

.btn {
    border-radius: 0.25em;
    border-width: 0.2em;
    font-size: 1.25em;
    padding: 0.5em;
    width: auto;
    height: 30px;
}
.btn-dark {
    border: solid $dark;
    color: $dark; 
}
.btn-light {
    border: solid #fff;
    color: #fff;
}
.btn-animated {
    background-color: #272635;
    color:#fff;
}

.btn-nav {
    border: solid #272635;
    color: #272635; 
    font-size: 1.15em;
    padding: 0.5em 0.5em 0 0.5em;
    margin-left: auto;
}
h1 {
    font-size: 36px;
    line-height: 40px;
    display: block;
}

h2 {
    font-size: 30px;
    line-height: 40px;
}

h3 {
    font-size: 24px;
    line-height: 40px;
}

h4 {
    font-size: 18px;
    line-height: 20px;
}

h5 {
    font-size: 14px;
    line-height: 20px;
}

h6 {
    font-size: 12px;
    line-height: 20px;
}

body {
    font-family: 'Manrope', 'Nanum Gothic', sans-serif;
}

h1, h2, h3 {
    font-family: 'Nanum Gothic', sans-serif;
}